.home .container {
	margin: 0;
	padding: 0 0 30px;
	text-align: center;
}
.home .container h1 {
	color: #fff;
	display: inline;
	font-family: poller_oneregular;
	font-size: 3rem;
	font-weight: normal;
	margin: 0;
	padding: 0;
	text-transform: uppercase;
}
.home .container h1 span {
	background: #252525;
	display: inline-block;
	padding: 15px 28px 12px;
}
.home .container > div {
	margin: 0 auto;
	padding: 0;
	/* width: 960px; */
}
.home .container > div img {
	/* border: 2px solid #292929; */
	margin: 24px 0 0;
	padding: 0;
}
.home .container > div .article {
	margin: 0 auto;
	padding: 30px 30px 0;
	width: 560px;
}
.home .container > div .article h2 {
	font-family: 'Arial Black', Gadget, sans-serif;
	font-size: 25px;
	font-weight: normal;
	margin: 0 0 30px;
}
.home .container > div .article h3 {
	color: #252525;
	line-height: 24px;
	margin: 0;
	padding: 0;
}
.home .container > div .article h4 {
	background: #252525;
	color: #fff;
	display: inline-block;
	font-family: 'Arial Black', Gadget, sans-serif;
	font-size: 25px;
	font-weight: normal;
	margin: 0 0 30px;
	padding: 8px 27px 10px;
}
.home .container > div .article p {
	margin: 0 0 30px;
}
.home .container div ul {
	margin: 0;
	padding: 24px 10px 0;
	width: 940px;
}
.home .container div ul li {
	border: 2px solid #1a1a1a;
	display: block;
	list-style: none;
	margin: 0 0 13px;
	overflow: hidden;
	padding: 0;
}

/* Gallery--------------------------------------- */

.gallery {
	display: flex;
	justify-content: space-around;
}

.gallery-media {
	list-style: none;
}

.gallery-wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	opacity: 0;
	transition: 0.5s;
}

.flex-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.photo-wrapper {
	width: 33.3333%;
	height: 300px;
	position: relative;
}

.photo {
	width: 100%;
	height: 100%;
}

.photo img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

