	font-size: 15px;
	font-weight: normal;
	margin: 0 0 37px;
	padding: 20px 10px;
	text-align: center;
	text-transform: uppercase;
	width: 596px;
}
.contact .container form textarea {
	height: 175px;
	overflow: auto;
	resize: none;
}
.contact .container form #send {
	background: #252525;
	color: #fff;
	font-family: 'Arial Black', Gadget, sans-serif;
	font-size: 15px;
	font-weight: normal;
