/* ------------------------------------------ */

section {
	width: 100%;
	max-width: 1024px;
	margin: 0 auto;
}

.content-padding {
	padding-bottom: 150px;
}

.section-title {
	font-size: 2rem;
}

.introduction {
	text-align: left;
}

.image-wrapper {
	width: 100%;
	height: 100vh;
	position: relative;
}

.image-wrapper .image {
	width: 100%;
	height: 100%;
	margin: 0 !important;
}

.main-title {
	position: absolute;
	top: 40%;
	left: 50%;
	transform: translate(-50%, -50%);
}

/* Blog.js-------------------------------------------- */
.blog-thumbnail {
	width: 300px;
	height: 200px;
	object-fit: cover;
	padding: 10px;
	float: left;
}

.blog-thumbnail img {
	width: 100%;
	height: 100%;
}

.blog-details {
	padding-top: 20px;
	float: right;
	text-align: left;
	display: flex;
	flex-direction: column;
	width: 60%;
}
